import { useState } from "react";
import styled from "styled-components";
import { space } from "styled-system";
import Box from "../box/box";
import Heading from "../heading/heading";
import Text from "../text/text";
import carrotLogo from "../../images/carrot.svg";

const Container = styled(Box)`
  border-top: 1px solid ${({ theme }) => theme.colors.grey[1]};
  &:last-child {
    border-bottom: 1px solid ${({ theme }) => theme.colors.grey[1]};
  }
`;

const Title = styled.button`
  display: block;
  text-align: left;
  width: 100%;
  padding: 0;
  -webkit-appearance: none;
  border: none;
  background: none;
  font: inherit;
  cursor: pointer;
  outline: none;
  user-select: none;
  &:hover {
    color: ${({ theme }) => theme.colors.primary[60]};
  }
  ${space}
`;

const FaqItemCollapsable = ({ question, answer }) => {
  const [isCollapsed, setIsCollapsed] = useState(true);

  return (
    <Container my={0} pt={2} pb={6}>
      <Box display="flex" justifyContent="space-between">
        <Box flex="1">
          <Heading as="h3" fontSize={4}>
            <Title pt={4} onClick={() => setIsCollapsed(!isCollapsed)}>
              {question ?? "How does a trial work?"}
            </Title>
          </Heading>
          <Text fontSize={2} mb={0} maxWidth="60rem" display={isCollapsed ? "none" : "block"}>
            {answer ??
              "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Suspendisse ut lacus vitae quam pretium facilisis. Aliquam vitae magna eu arcu commodo sodales."}
          </Text>
        </Box>
        <Box position="relative" top="20px" pl={3} pr={4}>
          <img
            width="10px"
            height="auto"
            src={carrotLogo}
            style={{
              transition: "transform .15s ease-out",
              transform: `rotate(${isCollapsed ? "0" : "90"}deg)`,
            }}
          />
        </Box>
      </Box>
    </Container>
  );
};

export default FaqItemCollapsable;
